

\::-webkit-scrollbar
    background: var(--scrollbar-track-color, #e1e1e1)
    width: var(--scrollbar-width, 15px)
    height: var(--scrollbar-height, 15px)
    border: var(--scrollbar-border, 2px solid #444)
    border-radius: 0


\::-webkit-scrollbar-thumb
    background: var(--scrollbar-thumb-color, #444)
    background-clip: padding-box
    border-radius: 0
