
.nav
    background: var(--nav-bg-color)
    width: 100%
    //border-bottom: #0e3d51 1px solid
    z-index: 100

    &--fixed
        position: fixed
        top: 0
        left: 0

    &--shadow
        box-shadow: var(--nav-shadow)

    &__trigger
        @media (max-width: 800px)
            float: right
            height: 38px
            width: 38px
            padding: 5px
            border: solid 2px var(--nav-trigger-color)
            display: flex
            flex-direction: column
            justify-content: space-evenly
            align-items: flex-start
            border-radius: 3px
            cursor: pointer
            text-decoration: none
            span
                height: 2px
                width: 100%
                background: var(--nav-trigger-color)
                border-radius: 3px

    &__main
        width: 100%
        display: flex
        justify-content: space-between
        align-items: center

    &__wrapper
        position: relative
        max-width: var(--max-screen-width)
        margin: 0 auto
        padding: 0 var(--padding)
        display: flex
        flex-direction: row
        align-items: center
        justify-content: space-between
        min-height: var(--nav-height)
        @media (max-width: 800px)
            //align-items: start
            flex-direction: column


    &__brand,
    &__brand a
        text-decoration: none
        color: var(--nav-brand-color)
        font-size: var(--nav-brand-size)

    &__brand
        line-height: var(--nav-height)

    &__responsive

        @media (max-width: 800px)
            display: none
            width: 100%
            overflow: hidden

        &.open
            display: flex
            margin-top: 5px
            margin-bottom: 10px

    &__link
        min-height: var(--nav-height)
        background: var(--nav-link-bg-color)
        @media (max-width: 800px)
            width: 100%

        &s
            display: flex
            flex: 1
            list-style: none
            margin: 0
            padding: 0
            width: 100%

            @media (max-width: 800px)
                width: 100%
                flex-direction: column

        a
            white-space: nowrap
            color: var(--nav-text-color)
            text-decoration: none
            display: flex
            align-items: center
            justify-content: center
            padding: var(--padding) 1.2rem
            height: 100%
            @media (max-width: 800px)
                justify-content: flex-start

        &:hover,
        &.active
            background: var(--nav-link-bg-color-active)
            a
                color: var(--nav-link-text-color-active)
