
.flex
    display: flex

.flex-inline
    display: inline-flex

.wrap
    flex-wrap: wrap

.nowrap
    flex-wrap: nowrap

$justify: (start: flex-start, end: flex-end, center: center, between: space-between, around: space-around, evenly: space-evenly, normal: normal)

$align: (start: flex-start, end: flex-end, center: center)

@each $key, $value in $justify
    .justify-#{$key}
        justify-content: $value

@each $key, $value in $align
    .align-#{$key}
        align-items: $value

@each $var, $val in $screens
    @media screen and (min-width: $val)
        @each $key, $value in $justify
            .#{var}-justify-#{$key}
                justify-content: $value

        @each $key, $value in $align
            .align-#{$key}
                align-items: $value
