$extra-colors: (
    "black": #000,
    "white": #fff,
    default: #d4dccd,
    success: green,
    danger: red,
    warning: orange,
    info: dodgerblue,
);

$full-color-set: map-merge($colors, $extra-colors);

:root {
    @each $key, $value in $full-color-set {
        --#{$key}-color: #{$value};
    }
}
